import React, { Component } from 'react';
import { connect } from 'react-redux';
import {  
    Box,
    Grid,
    withStyles,
    IconButton,
    Typography
} from '@material-ui/core';
import AutorenewIcon from '@material-ui/icons/Autorenew';

import { compose } from '../../utils';
import { withWeatherService } from '../hoc';
import { fetchMyCityWeather } from '../../actions';
import Spinner from '../spinner';
import MyCityCurrent from '../my-city-current';
import MyCityHourly from '../my-city-hourly';
import MyCityDaily from '../my-city-daily';

const styles = {
    gridContainer: {
        height: '100vh'
    },
    gridItem1: {
        padding: '2rem',
    },
    gridItem2: {
        padding: '5rem',
    },
    hr: {
        width: '100%',
        margin: '2rem 0 2rem 0'
    },
    box: {
        padding: '1rem 2rem .5rem',
        fontFamily: `'Merriweather', serif`
    },
    h2: {
        fontFamily: `'Roboto Slab', serif`
    }
}

const MyCityHeader = ({ timezone, classes, dt }) => {
    const myCityName = timezone.split('/')[1];
    const fullDate = new Date(dt * 1000);
    const month = fullDate.getMonth() + 1;
    const date = (fullDate.getDate() < 10 ? '0' : '') + fullDate.getDate();
    //const year = fullDate.getFullYear();
    const currentDate = `${date}.${month}`
    return (
        <Box display="flex" justifyContent="space-between" alignItems="end" className={classes.box}>
            <Box display="flex" flexDirection="column" alignItems="end">
                <IconButton aria-label="refresh" size="small">
                    <AutorenewIcon />
                </IconButton>
                {currentDate}
            </Box>
            <Typography variant="h3" component="h1" className={classes.h2}>
                {myCityName}
            </Typography>
        </Box>
    )
}

class MyCityContainer extends Component {
	componentDidMount() {
        this.props.fetchMyCityWeather();
	}

    render() {
        const { myCityWeather, loading, classes } = this.props;
        const { daily, hourly, current, timezone } = myCityWeather;
        //console.log(current)


        if (loading) {
            return <Spinner />;
        }

        // if (error) {
        //     return <ErrorIndicator />;
        // }

        return (
            <Grid container className={classes.gridContainer}>
                <Grid item xs={12} md={4} className={classes.gridItem1}>
                    <MyCityHeader timezone={timezone} classes={classes} dt={current.dt} />
                    <hr/>
                    <MyCityCurrent current={current}/>
                </Grid>
                <Grid item xs={12} md={8} className={classes.gridItem2}>
                    <MyCityHourly hourly={hourly} />
                    <hr className={classes.hr}/>
                    <MyCityDaily daily={daily} />
                </Grid>
            </Grid>
        );
    }
}

const mapSrateToProps = ({ myCityWeather, loading }) => {
    return { myCityWeather, loading }
}

const mapDispatchToProps = (dispatch, { weatherService }) => {
    return {
        fetchMyCityWeather: fetchMyCityWeather(weatherService, dispatch)
    }
}

export default compose(
    withStyles(styles),
	withWeatherService(),
	connect(mapSrateToProps, mapDispatchToProps)
)(MyCityContainer);